
 /* header css file */
 .PageBody {
    height: 680px;
    background: #fff5f1;
    border-radius: 40px;
    padding: 50px 100px 50px 100px;
  }
  
.TopPageNav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 52px;
  }

  .CompanyName {
    display: flex;
}

.Logo-First-Part {
    margin-top: 8px;
    height: 14px;
    width: 28px;
    border-radius: 2px;
    background: #524fd5;
    transform: skew(-40deg);
    rotate: 142deg;
  }
  
  .Logo-Second-Part {
    margin-top: 8px;
    margin-left: 21px;
    position: absolute;
    height: 14px;
    width: 28px;
    border-radius: 2px;
    background: #ffc0b4;
    transform: skew(-40deg);
    rotate: 142deg;
  }
  
.NavMid {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 550px;
    height: 36px;
    list-style: none;
}

.NavRight {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 12px 32px;
    width: 70px;
    background: #ffffff;
    border-radius: 73px;
}
.Font5 {
    width: 63px;
    height: 28px;
    font-family: "Arial";
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 28px;
    color: #181b32;
    margin-left: 30px;
}
 

  /* left part css file */

  .PageContent {
    margin-top: 50px;
    display: flex;
    justify-content: space-between;
  }


  .Description {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 25px;
    gap: 32px;
  }

  .Description1 {
    width: 558px;
    height: 213px;
    font-family: "Arial";
    font-style: normal;
    font-weight: 300;
    font-size: 56px;
    line-height: 126%;
    color: #181b32;
}

.TopPageBlueText {
    color: #524fd5;
}

.Description2 {
    width: 350px;
    height: 68px;
    font-family: "Arial";
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 190%;
    color: #666680;
}

.ButtonDescription3 {

        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        padding: 24px 32px;
        width: 90px;
        height: 12px;
        background: #524fd5;
        color: white;
        border-radius: 73px;
      
}


